class Global{
	static request(method, url ,data){
		return new Promise((resolve,reject)=>{
			let xhr = new XMLHttpRequest()
			xhr.addEventListener("load", ()=>{resolve(xhr)})
			xhr.addEventListener("error", ()=>{reject(xhr)})
			
			xhr.open(method, url)
			xhr.send(data)
		})
	}
	static async tagRead(){
		let data = {action:"read"}
		let xhr = await this.request("POST", "/tag", JSON.stringify(data) )
		return JSON.parse(xhr.responseText)
	}
	static async tagWrite(input){
		let data = {action:"write", data:input}
		let xhr = await this.request("POST", "/tag", JSON.stringify(data) )
		return JSON.parse(xhr.responseText)
	}
	static async getDB(){
		let xhr = await this.request("GET", "/db")
		return JSON.parse(xhr.responseText)
	}
	static async saveDB(newRegistry){
		let data = {action:"set", ...newRegistry}
		let xhr = await this.request("POST", "/db", JSON.stringify(data) )
		if(xhr.status == 200)
			return
		else
			throw "ERROR"
	}
	static async deleteDB(id){
		let data = {action:"delete", id}
		let xhr = await this.request("POST", "/db", JSON.stringify(data) )
		if(xhr.status == 200)
			return
		else
			throw "ERROR"
	}
	static async isAutoOn(){
		let xhr = await this.request("GET", "/auto")
		if(xhr.status == 200)
			return JSON.parse(xhr.responseText)
		else
			throw "ERROR"
	}
	static async turnAuto(bool){
		let data = {action: bool? "enable" : "disable"}
		let xhr = await this.request("POST", "/auto", JSON.stringify(data) )
		if(xhr.status == 200)
			return
		else
			throw "ERROR"
	}
	static subscribeToHistory(callback){
		return new Promise(resolve=>{
			let source = new EventSource('/liveRegistry')
			
			source.addEventListener('message', function(e) {
				callback(null, e)
			}, false)
			source.addEventListener('error', function(e) {
				callback(e, null)
			}, false)
			
			resolve(source)
		})
	}
	static async getHistoryDates(){
		let data = {action: "list"}
		let xhr = await this.request("POST", "/history", JSON.stringify(data) )
		if(xhr.status == 200)
			return JSON.parse(xhr.responseText)
		else
			throw "ERROR"
	}
	static async getHistory(date = null){
		let data = {action: (date == null)? "current" : "from_date", date }
		let xhr = await this.request("POST", "/history", JSON.stringify(data) )
		if(xhr.status == 200)
			return JSON.parse(xhr.responseText)
		else
			throw "ERROR"
	}
}


export default Global


