import React, { Component } from 'react';
import {Alert, Panel, Button, FormControl, InputGroup} from 'react-bootstrap';
import Global from '../Global'

export default class TagPage extends Component{
	constructor(props){
		super(props)
		this.state = {
			dataFromTag: null,
			readError: false,
			isReading:false,
			isWriting:false,
			writeError:false,
			writeButtonClickedBefore:false,
			inputLength: 0,
			inputValue: ""
		}
		
		this.readClick = this.readClick.bind(this)
		this.writeClick = this.writeClick.bind(this)
		this.inputChanged = this.inputChanged.bind(this)
	}
	render(){
		return (
			<div>
				<Panel>
					<Panel.Body>
						<Button disabled={this.props.busy} onClick={this.readClick} bsStyle={(this.state.isReading? "info": "default")}>
							Read
						</Button>
						<br/><br/>
						{this.displayReadAlert()}
					</Panel.Body>
				</Panel>
				
				<Panel>
					<Panel.Body>
						<InputGroup>
							<FormControl onChange={this.inputChanged} disabled={this.props.busy} type="text" placeholder="Enter a new key/ID for the tag" />
						<InputGroup.Button>
							<Button onClick={this.writeClick} bsStyle={(this.state.isWriting? "info": "default")} disabled={this.props.busy || this.state.inputLength < 1}>
								Write
							</Button>
						</InputGroup.Button>
						</InputGroup>
						<br/><br/>
						{this.displayWriteAlert()}
					</Panel.Body>
				</Panel>
				
			</div>
		)
	}
	inputChanged(e){
		this.setState({inputLength: e.target.value.length, inputValue: e.target.value})
	}
	displayReadAlert(){
		let display = this.state.readError? <strong>Couldn´t read tag!</strong> : this.state.dataFromTag
		return (this.state.dataFromTag == null)? null : 
			<Alert bsStyle={this.state.readError? "danger" : "info"}>
				{display}
			</Alert> 
	}
	displayWriteAlert(){
		return this.state.writeButtonClickedBefore?
			<Alert bsStyle={this.state.writeError? "danger" : "success"}>
							<strong>{this.state.writeError? "Can't write to tag": "Success!"}</strong>
						</Alert>
			: null
	}
	async readClick(){
		this.setState({isReading:true})
		this.props.set_busy()
		let data = await Global.tagRead()
		this.setState({readError: !data.success, dataFromTag: data.data, isReading:false})
		this.props.unset_busy()
	}
	async writeClick(){
		this.setState({isWriting:true})
		this.props.set_busy()
		let data = await Global.tagWrite(this.state.inputValue)
		console.log(data)
		this.setState({writeError: !data.success, isWriting:false, writeButtonClickedBefore:true})
		this.props.unset_busy()
	}
}




