import React, { Component } from 'react';
import {Route, Link } from "react-router-dom";
import {Navbar, Nav, NavItem, Panel, PanelGroup} from 'react-bootstrap';

import './App.css';
import TagPage from './pages/TagPage'
import DatabasePage from './pages/DatabasePage'
import HistoryPage from './pages/HistoryPage'
import {DefaultAppConnect} from './redux'
import Global from './Global'
import nfc_logo from './NFC_logo.png'

class Top extends Component{
	constructor(props){
		super(props)
		this.state = {isAutoRegistryOn:false, waitingResponse: false}
		
		this.checkAutoState = this.checkAutoState.bind(this)
		
		
	}
	componentDidMount(){
		this.checkAutoState()
	}
	render(){
		return(
		<Navbar staticTop className="top"  fluid={true} inverse >
		<Navbar.Header>
			<Navbar.Brand>
				<a href="#">NFC Registry System</a>
			</Navbar.Brand>
		</Navbar.Header>
		<Nav bsStyle="tabs" >
			<NavItem eventKey={1} disabled = {this.props.auto_on} href="#tag">Tag</NavItem>
			<NavItem eventKey={2} disabled = {this.props.auto_on} href="#database">Database</NavItem>
			<NavItem eventKey={3} disabled = {this.props.busy} href="#/" onClick={()=>{
				this.toggleAuto()
			}}>
				{`${this.props.auto_on? "Disable" : "Enable"} Registry`}
			</NavItem>
			<NavItem eventKey={4} href="#history">History</NavItem>
		</Nav>
		</Navbar>
		)
	}
	async checkAutoState(){
		let data = await Global.isAutoOn()
		if(data === true)
			this.props.set_auto()
		if(data === false)
			this.props.unset_auto()
	}
	async toggleAuto(){
		this.props.set_busy()
		const currentState = this.props.auto_on
		await this.checkAutoState()
		if(currentState !== this.props.auto_on)
			return this.props.unset_busy()
		else{
			await Global.turnAuto(!currentState)
			await this.checkAutoState()
			return this.props.unset_busy()
		}
	}
}

const roottab = () =>{
	return(
	<Panel bsStyle="info">
	<Panel.Body>
		<img id="nfclogo" src={nfc_logo}></img>
		<br/>
		<p className="middle">
			This system is compatible with NTAG2XX NFC tags.
			<br/>
			This is a prototype.
		</p>
		
	</Panel.Body>
	</Panel>
	)
}

class Body extends Component{
	constructor(props){
		super(props)
	}
	render(){
		return(
			<div className="marginTop">
				<Route exact path="/" component={roottab} />
				<Route path="/tag" component={DefaultAppConnect(TagPage)} />
				<Route path="/database" component={DefaultAppConnect(DatabasePage)} />
				<Route path="/history" component={DefaultAppConnect(HistoryPage)} />
			</div>
		)
	}
}

class App extends Component {
  render() {
	const NewTop = DefaultAppConnect(Top)
    return (
      <div className="App">
	  <NewTop/>
		<div className="paddingCenter">
			<Body/>
		</div>
	</div>
    );
  }
}

export default App;



