import React, { Component } from 'react';
import {Panel, Button, ButtonGroup, Table, OverlayTrigger, Popover, FormGroup, ControlLabel, FormControl, Alert} from 'react-bootstrap';
import Global from '../Global'

export default class DatabasePage extends Component{
	constructor(props){
		super(props)
		this.state = {
			tableData : null,
			currentRowData: null,
			inputID: "",
			inputName:""
		}
		this.fetchData = this.fetchData.bind(this)
		this.rowDataRef = this.rowDataRef.bind(this)
		
		this.saveValueToState = this.saveValueToState.bind(this)
		this.onUpdate = this.onUpdate.bind(this)
		this.onDelete = this.onDelete.bind(this)
		this.addToDB = this.addToDB.bind(this)
	}
	componentDidMount(){
		this.fetchData()
	}
	render(){return(
		<div>
			<Panel bsStyle="info" defaultExpanded>
				<Panel.Heading>
					<Panel.Title toggle componentClass="h3">Current Database</Panel.Title>
				</Panel.Heading>
				<Panel.Collapse>
				<Panel.Body>
					<DatabaseTable
						rowDataRef = {this.rowDataRef}
						data={this.state.tableData}
						onUpdate={this.onUpdate}
						onDelete={this.onDelete}
						busy={this.props.busy}
					/>
				</Panel.Body>
				</Panel.Collapse>
			</Panel>
			
			<Panel bsStyle="default">
				<Panel.Body>
					<FormGroup>
						<ControlLabel>ID</ControlLabel>
						<FormControl value={this.state.inputID} onChange={this.saveValueToState("inputID")} ></FormControl>
					</FormGroup>
					<FormGroup>
						<ControlLabel>Name</ControlLabel>
						<FormControl value={this.state.inputName} onChange={this.saveValueToState("inputName")}></FormControl>
					</FormGroup>
					<Button onClick={this.addToDB} disabled={this.state.inputID.length < 1 || this.state.inputName.length < 1 || this.props.busy}>Save</Button>
				</Panel.Body>
			</Panel>
		</div>
	)}
	saveValueToState(stateProp){
		let func = (e)=>{
			let state = {}
			state[stateProp] = e.target.value
			this.setState(state)
		}
		return func.bind(this)
	}
	async fetchData(){
		this.props.set_busy()
		let data = await Global.getDB()
		this.setState({tableData: data.data})
		this.props.unset_busy()
	}
	rowDataRef(el){
		this.setState({currentRowData: el})
	}
	onUpdate(){
		this.setState({inputID: this.state.currentRowData.id, inputName: this.state.currentRowData.name})
	}
	async onDelete(){
		this.props.set_busy()
		await Global.deleteDB(this.state.currentRowData.id)
		let data = await Global.getDB()
		this.setState({tableData: data.data})
		this.props.unset_busy()
	}
	async addToDB(){
		this.props.set_busy()
		await Global.saveDB({id: this.state.inputID, name: this.state.inputName})
		let data = await Global.getDB()
		this.setState({tableData: data.data})
		this.props.unset_busy()
		
	}
}

const DatabaseTable = ({data, onDelete, onUpdate, rowDataRef, busy}) =>{
	if(data == null)
		return null
	if(data.length == 0)
		return <Alert bsStyle="warning">
			<strong>Empty Database!</strong></Alert>
	const style = {
		cursor:"pointer"
	}
	const popover = (
		<Popover id="popover-db-table-1" >
			<ButtonGroup>
				<Button onClick={onUpdate} bsStyle="info" >Copy</Button>
				<Button disabled={busy} onClick={onDelete} bsStyle="danger" >Delete</Button>
			</ButtonGroup>
		</Popover>
	);
	
	let dataAsRows = data.map( (el,i)=>
		<OverlayTrigger
			trigger="click"
			rootClose
			placement="bottom"
			overlay={popover} key={i}>
		<tr style={style} onClick={
			(rowDataRef == null)? null : ()=>{
				rowDataRef(el)
			}
		} >
			<td>{i+1}</td>
			<td>{el.id}</td>
			<td>{el.name}</td>
		</tr></OverlayTrigger> )
	
	
	return(
		<Table responsive striped bordered condensed hover>
		<thead>
			<tr>
				<th>#</th>
				<th>ID</th>
				<th>Name</th>
			</tr>
		</thead>
		<tbody>
			{dataAsRows}
		</tbody>
		</Table>
	)
}


