import React, { Component } from 'react';
import {Button, Navbar, NavItem, Nav, Panel, Alert, Table} from 'react-bootstrap';
import {Route} from "react-router-dom";

import Global from '../Global';

export default class HistoryPage extends Component{
	constructor(props){
		super(props)
	}
	render(){
		return(
		<div>
			<Navbar>
				<Nav><NavItem eventKey={1} href="#/history">Dates</NavItem></Nav>
				<Nav><NavItem eventKey={1} href="#/history/today">Today</NavItem></Nav>
			</Navbar>
			<Route exact path="/history" component={dateList} />
			<Route exact path="/history/d/:date" component={tableFromDate} />
			<Route exact path="/history/today" component={LiveTable} />
		</div>
		);
	}
}

class LiveTable extends Component{
	constructor(props){
		super(props)
		this.state = {
			history:null,
			nfcEvents:[]
		}
		this.initEvents = this.initEvents.bind(this)
	}
	componentDidMount(){
		this.initEvents()
	}
	render(){
		let toRender = "Loading..."
		if(this.state.history !== null){
			if(this.state.history.length > 0){
				toRender = this.getTable()
			}else{
				toRender = "Empty database"
			}
		}
		return(
		<Panel >
			<Panel.Body>
			{toRender}
			</Panel.Body>
		</Panel>
		)
	}
	componentWillUnmount(){
		this.eventSource.close()
	}
	async initEvents(){
		this.eventSource = await Global.subscribeToHistory((err, e)=>{
			if(err !== null)
				console.log(err)
			else{
				//let evts = [... this.state.nfcEvents]
				//evts.push({id: e.id, reg_date: e.lastEventId})
				//this.setState({nfcEvents: evts})
				if(this.state.history != null)
					this.setState({history: [... this.state.history, 
						{id: e.data, reg_date: e.lastEventId, name: this.dbnames[e.data] }
					]})
			}
		})
		
		let data = await Global.getHistory()
		let db = await Global.getDB()
		data = data.data
		db = db.data
		let names = {}
		db.forEach(el=>{
			names[el.id] = el.name
		})
		data.forEach(el=>{
			el.name = names[el.id]
		})
		
		this.setState({history: data})
		
		this.dbnames = names
	}
	getTable(){
		let dataAsRows = this.state.history.map((el, i)=><tr key={i}>
				<td>{i+1}</td>
				<td>{el.id}</td>
				<td>{el.name}</td>
				<td>{el.reg_date}</td>
			</tr>)
		return <Table responsive striped bordered condensed hover>
		<thead>
			<tr>
				<th>#</th>
				<th>ID</th>
				<th>Name</th>
				<th>Date</th>
			</tr>
		</thead>
		<tbody>
			{dataAsRows}
		</tbody>
		</Table>
	}
}

class dateList extends Component{
	constructor(props){
		super(props)
		this.state = {
			dates: null
		}
		this.fetch = this.fetch.bind(this)
	}
	componentDidMount(){
		this.fetch()
	}
	async fetch(){
		let data = await Global.getHistoryDates()
		this.setState({dates: data.data})
	}
	render(){
		let toRender = null
		if(this.state.dates == null ){
			toRender = "Loading..."
		}
		else if(this.state.dates.length > 0){
			let dates = this.state.dates.map((el,i)=><li key={i} ><a href={`#/history/d/${el}`}>{el}</a></li>)
			toRender = <ul>{dates}</ul>
		}
		else if (this.state.dates.length == 0){
			toRender = <Alert bsStyle="warning"><strong>
				No previous registries
			</strong></Alert>
		}
		return(
		<Panel >
			<Panel.Body>
			{toRender}
			</Panel.Body>
		</Panel>
		)
	}
}
/*
const tableFromDate = ({match}) =>{
	return (<div>{match.params.date}</div>)
}
*/
class tableFromDate extends Component{
	constructor(props){
		super(props)
		this.state = {
			tableData : null
		}
		
		this.tableDate = props.match.params.date
		this.fetch = this.fetch.bind(this)
	}
	componentDidMount(){
		this.fetch()
	}
	async fetch(){
		let data = await Global.getHistory(this.tableDate)
		let db = await Global.getDB()
		data = data.data
		db = db.data
		let names = {}
		db.forEach(el=>{
			names[el.id] = el.name
		})
		data.forEach(el=>{
			el.name = names[el.id]
		})
		this.setState({tableData: data})
	}
	render(){
		let toRender = "Loading..."
		if(this.state.tableData != null){
			if(this.state.tableData.length > 0){
				toRender = this.getTable()
			}
			else 
				toRender = "Invalid data"
		}
		
		return(
			<Panel >
				<Panel.Body>
					{toRender}
				</Panel.Body>
			</Panel>
		)
	}
	getTable(){
		let dataAsRows = this.state.tableData.map((el, i)=><tr key={i}>
				<td>{i+1}</td>
				<td>{el.id}</td>
				<td>{el.name}</td>
				<td>{el.reg_date}</td>
			</tr>)
		return <Table responsive striped bordered condensed hover>
		<thead>
			<tr>
				<th>#</th>
				<th>ID</th>
				<th>Name</th>
				<th>Date</th>
			</tr>
		</thead>
		<tbody>
			{dataAsRows}
		</tbody>
		</Table>
	}
}


