import { connect } from 'react-redux'

const initialState = {
	server_busy: false,
	fatal_error: false,
	auto_on: false
}

export const AppActions = {
	SET_BUSY: {type:"SET_BUSY", value: true},
	UNSET_BUSY: {type:"SET_BUSY", value: false},
	AUTO_ON: {type:"SET_AUTO", value: true},
	AUTO_OFF: {type:"SET_AUTO", value: false}
}

export const AppReducer = (state = initialState, action)=>{
	if (typeof state === 'undefined') return initialState
	
	switch(action.type){
		case "SET_BUSY":
			return Object.assign({}, state, {server_busy: action.value})
		case "SET_AUTO":
			return Object.assign({}, state, {auto_on: action.value})
	}
	
	return state
}

export const DefaultAppConnect = (component) => connect(
		(state, ownProps)=>({
			busy: state.server_busy,
			auto_on : state.auto_on
		}),
		(dispatch, ownProps)=>({
			set_busy: ()=>dispatch(AppActions.SET_BUSY),
			unset_busy: ()=>dispatch(AppActions.UNSET_BUSY),
			set_auto: ()=>dispatch(AppActions.AUTO_ON),
			unset_auto: ()=>dispatch(AppActions.AUTO_OFF)
		})
)(component)


